/* pelota */
#pelota_dos {
    position: absolute;
    animation: rebotaHorizontal 1s alternate infinite ease-out;}

@keyframes rebotaHorizontal {
    0% {
        left: 100%;  /* Empieza a la derecha de la pantalla */
        height: 50px;
    }
    50% {
        left: 50%;  /* En el 50% de la animación estará a la mitad de la pantalla */
        height: 100px;
    }
    100% {
        left: 0%;  /* Al final, estará en el lado izquierdo de la pantalla */
        height: 50px;
    }
}

#pelota_tres {
    position: absolute;
    animation: rebotaHorizontal 1s alternate infinite ease-out;
    top: 400px;}

@keyframes rebotaHorizontal {
    0% {
        right: 100%;  
        height: 50px;
    }
    50% {
        right: 50%;  
        height: 100px;
    }
    100% {
        right: 0%;  
        height: 50px;
    }
}

#pelota_cuatro{
    position: absolute;
    left: 70%; /* Más hacia la derecha */
    animation: subirRebote 1s alternate infinite ease-out;}

@keyframes subirRebote {
    0% {
        top: 90%;  
        height: 50px;
    }
    50% {
        top: 50%;  
        height: 80px;
    }
    100% {
        top: 10%;  
        height: 50px;
    }
}

#pelota_cinco{
    position: absolute;
    right: 70%; 
    animation: pelota_cinco 1s alternate infinite ease-out;}

@keyframes pelota_cinco {
    0% {
        top: 90%; 
        height: 50px;
    }
    50% {
        top: 50%; 
        height: 80px;
    }
    100% {
        top: 10%;  
        height: 50px;
    }
}

